.uxora-career{
    &-d-thumb{
        padding-bottom: 60px;
        @include max (767px){
            padding-bottom: 40px;
        }
    }
    &-d-sidebar{
        padding: 40px;
        border-radius: 20px;
        margin-left: 80px;
        position: sticky;
        transition: all 0.4s;
        top: 120px;
        background-color: var(--heading-bg);
        @include max (1199px){
            margin-left: 0;
            margin-top: 40px;
        }
        ul{
            li{
                position: relative;
                padding-left: 20px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    width: 12px;
                    height: 12px;
                    top: 10px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--body-color);
                }
                h4{
                    color: var(--light-color);
                    padding-bottom: 5px;
                }
                p{
                    color: var(--light-color);
                }
            }
        }
    }
}
