.uxora-blog{
    &-wrap{
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color2);
        &:hover{
            .uxora-blog-thumb img{
                transform: scale(1.1);
            }
            .uxora-blog-content{
                h3{
                    background-size: 100% 1px;
                }
            }
        }
        &.wrap2{
            margin-right: 50px;
            @include max (991px){
                margin-right: 0;
            }
        }
    }
    &-thumb{
        border-radius: 15px;
        margin-bottom: 24px;
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-meta{
        padding-bottom: 24px;
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                position: relative;
                padding-left: 30px;
                @include max (767px){
                    margin-bottom: 15px;
                }
                &:last-child{
                    margin-right: 0;
                    margin-bottom: 0;
                }
                
                a{
                    font-size: 18px;
                    line-height: 25px;
                    color: var(--heading-color);
                    img{
                        position: absolute;
                        top: 4px;
                        left: 0;
                    }
                }
            }
        }
    }
    &-content{
        padding-bottom: 30px;
        a{
            color: var(--heading-color);
        }
        h3{
            padding-top: 0;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
        }
    }
    // strat inner page
    &-widgets {
        margin-bottom: 24px;
        background-color: var(--light-color2);
        padding: 30px;
        border-radius: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        h3{
            padding-bottom: 24px;
        }
        @include max (991px){
            margin-bottom: 40px;
        }
        &.widgets2{
            h3{
                padding-bottom: 12px;
            }
        }
    }
    &-categorie{
       ul{
        li{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
            a{
                font-size: 21px;
                line-height: 30px;
                transition: all 0.4s;
                color: var(--heading-color);
                &:hover{
                    font-weight: 700;
                }
            }
        }
       }
    }
    &-recent-post-item{
        display: flex;
        align-items: center;
        gap: 15px;
        margin-bottom: 24px;
        &:last-child{
            margin-bottom: 0;
        }
        &:hover{
            .uxora-blog-recent-post-thumb img{
                transform: scale(1.1);
            }
            .uxora-blog-recent-post-data{
                h5{
                    background-size: 100% 1px;
                }
            }
        }
    }
    &-recent-post-thumb {
        border-radius: 10px;
        overflow: hidden;
        flex-shrink: 0;
        transition: all 0.4s;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-recent-post-data{
        ul {
            li{
                font-size: 18px;
                line-height: 25px;
                color: var(--heading-color);
                margin-bottom: 10px;
                position: relative;
                padding-left: 30px;
                img{
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }
        h5{
            margin-top: 5px;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
            padding-bottom: 0;
            font-size: 20px;
            line-height: 28px;
        }
    }
    &-tags{
        ul{
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            li{
                display: inline-block;
                a{
                    font-size: 16px;
                    line-height: 24px;
                    padding: 8px 22px;
                    display: block;
                    border-radius: 50px;
                    transition: all .4s;
                    color: var(--heading-color);
                    background-color: var(--light-color);
                    &:hover{
                        color: var(--white-color);
                        background-color: var(--accent-color);
                    }
                }
            }
        }
    }
    // single page
    &-d-thumb{
        padding-bottom: 24px;
    }
    &-d-content{
        padding-top: 40px;
        h2{
            padding-bottom: 20px;
        }
        a{
            color: var(--heading-color);
        }
        ul{
            li{
                font-weight: 500;
                position: relative;
                margin-bottom: 15px;
                padding-left: 30px;
                &::before{
                    content: "";
                    position: absolute;
                    top: 10px;
                    left: 0;
                    width: 10px;
                    height: 10px;
                    border-radius: 100px;
                    background-color: var(--heading-color);
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
    &-d-content-wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 0;
        border-top: 1px solid #142D6F1A;
        border-bottom: 1px solid #142D6F1A;
        margin: 40px 0;
        @include max (767px){
            margin: 30px 0;
            display: block;
        }
    }
    &-d-comment-box{
        h4{
            padding-bottom: 30px;
        }
    }
    &-d-comment-wrap1{
        display: flex;
        .reply-btn a{
            font-weight: 500;
            padding-top: 10px;
            color: var(--heading-color);
        }
        &.wrap2{
            padding-bottom: 24px;
            border-bottom: 1px solid #142D6F1A;
            margin-bottom: 24px;
        }
    }
    &-d-comment-wrap{
        display: flex;
        padding-bottom: 24px;
        border-bottom: 1px solid #142D6F1A;
        margin-bottom: 30px;
        padding-top: 24px;
        border-top: 1px solid #142D6F1A;
        margin-top: 30px;
        .reply-btn a{
            font-weight: 500;
            padding-top: 10px;
            color: var(--heading-color);
        }
    }
    &-d-comment-thumb{
        margin-right: 20px;
        flex-shrink: 0;
        img{
            border-radius: 100px;
        }
    }
    &-d-comment-data1{
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            padding-top: 15px;
        }
    }
    &-d-comment-data{
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            color: var(--dark-color1);
            padding-top: 10px;
        }
    }
    &-d-comment-box2{
        h4{
            padding-bottom: 30px;
        }
    }
    &-d-content-comment-box{
        h4{
            margin-bottom: 30px;
        }
    }
}



.mt-10{
    margin-top: 10px;
}
.uxora-recent-post-item:hover .uxora-recent-post-data h6 {
    background-size: 100% 1px;
}
.uxora-search-box {
    position: relative;
    padding: 0 60px 0 0;
    border-radius: 50px;
    background-color: var(--light-color2);
    input{
        font-size: 16px;
        line-height: 24px;
        padding: 12px 0 12px 24px;
    }
}
button#uxora-search-btn {
    position: absolute;
    right: 5px;
    top: 5px;
    width: 50px;
    height: 50px;
    border-radius: 100px;
    background-color: var(--accent-color);
    .ri-search-line{
        color: var(--white-color);
    }
}
blockquote {
    margin-top: 40px;
    position: relative;
    border-radius: 10px;
    padding: 30px;
    background-color: var(--light-color);
    &::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 6px;
        height: 100%;
        border-radius: 10px 0px 0px 10px;
        background-color: var(--heading-bg);
    }
}
.uxora-blog-d-thumb {
    img{
        width: 100%;
    }
}
.mt-41{
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}