.uxora-contact-us{
    &-info-item{
        display: flex;
        gap: 24px;
        padding-bottom: 40px;
    }
    &-icon{
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 5px;
        justify-content: center;
        background-color: var(--body-color);
    }
    &-content{
        a{
            color: var(--heading-color);
        }
    }
}
.uxora-contact{
    &-box{
        h4{
            padding-bottom: 24px;
        }
        &.box2{
            @include max (991px){
                margin-top: 40px;
            }
            @include max (767px){
                margin-top: 30px;
            }
        }
    }
    &-us-map {
        width: 100%;
        height: 750px;
        @include max (991px){
            height: 400px;
        }
    }
}
