.uxora-skill{
    &-item{
        margin-bottom: 20px;
    }
    &-title{
        h6{
            font-weight: 500;
            margin-bottom: 10px;
        }
    }
    &-line{
        position: relative;
        border: 7px solid #142D6F1A;
        transform: scaleX(0);
        transform-origin: left;
        border-radius: 30px;
        animation: animate 1s cubic-bezier(1,0,.5,1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0!important;
        }
    }
    &-bar{
        margin: -7px 0px 0 -10px;
        position: absolute;
        border-radius: 10px;
        transform-origin: left;
        background: var(--body-color);
        animation: animate 1s 1s cubic-bezier(1,0,.5,1) forwards;
        border: 7px solid var(--body-color);
    &::before{
        position: absolute;
        content: "";
        height: 0;
        right: 0;
        top: -14px;
        width: 0;
        border: 7px solid transparent;
        border-bottom-width: 0px;
        border-right-width: 0px;
        border-top-style: #f0f0f0;
    }
    &::after{
        position: absolute;
        right: 0;
        top: -50px;
        color: var(--heading-color);
        font-weight: 500;
        padding: 1px 8px;
        border-radius: 3px;
    }
    }
    &-line2{
        position: relative;
        border: 7px solid #142D6F1A;
        transform: scaleX(0);
        transform-origin: left;
        border-radius: 30px;
        animation: animate 1s cubic-bezier(1,0,.5,1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0!important;
        }
    }
    &-bar2{
        margin: -7px 0px 0 -10px;
        position: absolute;
        border-radius: 10px;
        transform-origin: left;
        background: var(--body-color);
        animation: animate 1s 1s cubic-bezier(1,0,.5,1) forwards;
        border: 7px solid var(--body-color);
    &::before{
        position: absolute;
        content: "";
        height: 0;
        right: 0;
        top: -14px;
        width: 0;
        border: 7px solid transparent;
        border-bottom-width: 0px;
        border-right-width: 0px;
        border-top-style: #f0f0f0;
    }
    &::after{
        position: absolute;
        right: 0;
        top: -50px;
        color: var(--heading-color);
        font-weight: 500;
        padding: 1px 8px;
        border-radius: 3px;
    }
    }
}

.bar-one{
    width: 85%;
}
.bar-one::after{
    content: "85%";
}
.bar-two{
    width: 99%;
}
.bar-two::after{
    content: "99%";
}
.bar-three{
    width: 92%;
}
.bar-three::after{
    content: "92%";
}
.bar-four{
    width: 86%;
}
.bar-four::after{
    content: "86%";
}
.bar-five{
    width: 70%;
}
.bar-five::after{
    content: "70%";
}
.bar-six{
    width: 85%;
}
.bar-six::after{
    content: "85%";
}




@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}
