.uxora-error{
    &-section{
        padding: 250px 0 130px;
        
        @include max (991px){
            padding: 180px 0 100px;
        }
        @include max (767px){
            padding: 150px 0 80px;
        }
    }
    &-thumb{
        padding-bottom: 24px;
    }
    &-content{
        max-width: 700px;
        text-align: center;
        margin: 0 auto;
        h1{
            padding-bottom: 20px;
        }
    }
 
}