.uxora-about-us{
    &-thumb{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 20px;
        }
        &.thumb2{
            text-align: right;
            @include max (991px){
                text-align: left;
            }
        }
    }
    &-number{
        position: absolute;
        top: 0;
        z-index: 1;
        left: 48%;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        transition: all 0.4s;
        background-color: var(--heading-color);
        h3{
            color: var(--white-color);
        }
        @include max (1399px){
            display: none;
        }
    }
    
    
}
.pl-128{
    padding-left: 128px;
    @include max (1399px){
        padding-left: 26px;
    }
    @include max (991px){
        padding-left: 0;
    }
}
.pr-128{
    padding-right: 128px;
    @include max (1399px){
        padding-right: 26px;
    }
    @include max (991px){
        padding-right: 0;
    }
}
.pb-80{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
}
.border-r{
    position: relative;
    &::before{
        top: 0;
        left: 50%;
        width: 5px;
        height: 480px;
        content: "";
        position: absolute;
        background-color: #181B0E1A;
        @include max (1399px){
            display: none;
        }
    }
    &.r2{
        &::before{
            height: 400px;
        }
    }
}

@keyframes updown {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-30px);
	}
	100% {
		transform: translatey(0px);
	}
}

.one {
    top: 130px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.two {
    top: 80px;
}
.three {
    top: 110px;
}

.four {
    top: 140px;
}