.uxora-video{
    &-section{
        h2{
            color: var(--light-color);
            padding-bottom: 20px;
        }
        p{
            color: var(--light-color);
            margin: 0;
        }
    }
    &-thumb{
        position: relative;
        @include max (991px){
            text-align: center;
            padding-bottom: 40px;
        }
        .play-btn{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    &-process-wrap{
        margin-top: 100px;
        @include max (991px){
            margin-top: 80px;
        }
        @include max (767px){
            margin-top: 60px;
        }
       
    }
    &-process-number{
        h2{
            color: var(--light-color);
        }
    }
    // end one
    &-section2{
        position: relative;
        margin-top: -376px;
        @include max (1399px){
            margin-top: -276px;
        }
        @include max (991px){
            margin-top: -276px;
        }
        @include max (767px){
            margin-top: 50px;
        }
    }
    &-thumb2{
        position: relative;
        img{
            height: 542px;
            border-radius: 10px;
            @include max (1399px){
                height: 460px;
            }
            @include max (1399px){
                height: 390px;
            }
            @include max (1399px){
                height: 100%;
            }
        }
        .play-btn{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            img{
                height: 100%;
                @include max (991px){
                    padding-left: 10px;
                    padding-top: 10px;
                }
            }
        }
    }
}
.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background-color: var(--heading-bg);
    opacity: 0;
    border-radius: 100%;
    left: -65px;
    top: -65px;
    z-index: 0;
    transform: translate(-50px, -50px);
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    @include max(991px) {
      width: 160px;
      height: 160px;
      left: -40px;
      top: -40px;
    }
    &.wave-1 {
      -webkit-animation-delay: 0s;
      animation-delay: 0s;
    }
    &.wave-2 {
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
    &.wave-3 {
      -webkit-animation-delay: 2s;
      animation-delay: 2s;
    }
    &.waves2{
        @include max(767px) {
            left: -30px;
            top: -30px;
          }
    }
}
@keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }
  
    50% {
        opacity: 0.9;
    }
  
    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
}
