.uxora-iconbox{
    &-wrap{
        padding: 20px;
        border-radius: 10px;
        transition: all .4s;
        position: relative;
        display: block;
        overflow: hidden;
        z-index: 0;
        background-color: var(--light-color);
        margin-bottom: 24px;
        background-size: cover;
        background-position: center;
        &::before {
            content: "";
            left: 0;
            top: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            transition: all .4s;
            position: absolute;
            background: var(--light-color);
        }
        &:hover::before {
            opacity: 0;
        }
        &:hover{
            .uxora-iconbox-icon{
                path{
                    fill: var(--white-color);
                }
            }
            .uxora-iconbox-content{
                h5,p{
                    color: var(--white-color);
                }
            }
            .uxora-iconbox-btn{
                a{
                    color: var(--white-color);
                    path{
                        stroke: var(--white-color);
                    }
                }
            }
        }
    }
    &-icon{
        padding-bottom: 20px;
        path{
            transition: all 0.4s;
        }
    }
    &-content{
        padding-bottom: 24px;
        transition: all 0.4s;
        h5{
            transition: all 0.4s;
            margin-bottom: 15px;
        }
        p{
            transition: all 0.4;
        }
    }
    &-btn{
        a{
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                margin-left: 5px;
            }
        }
    }
}