.uxora-list-icon{
    &-content{
        ul{
            li{
                font-size: 21px;
                line-height: 1.43em;
                font-weight: 600;
                margin-bottom: 15px;
                position: relative;
                padding-left: 35px;
                color: var(--heading-color);
                @include max (575px){
                    font-size: 18px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }
        &.content2{
            padding-bottom: 24px;
            ul{
                li{
                    font-size: 18px;
                    padding-left: 32px;
                    img{
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }
        }
    }
    &-wraper{
        display: flex;
        gap: 50px;
        align-items: center;
        transition: all 0.4s;
        padding-bottom: 24px;
        border-bottom: 1px solid #001A3D1A;
        margin-bottom: 24px;
        @include max (767px){
            gap: 30px;
        }
        @include max (575px){
            display: block;
        }
        &:last-child{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
        &:hover{
            .uxora-list-icon-btn{
                path{
                    fill: var(--accent-color);
                    stroke: var(--accent-color);
                }
            }
        }
    }
    &-wrap{
        display: flex;
        gap: 24px;
        @include max (575px){
            display: block;
            padding-bottom: 15px;
        }
        &.wrap2{
            padding: 20px;
            border-radius: 10px;
            margin-bottom: 24px;
            background-color: var(--white-bg);
            &:last-child{
                margin-bottom: 0;
            }
            @include max (767px){
                display: flex;
            }
        }
        &.wrap3{
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    &-icon2{
        flex-shrink: 0;
        @include max (575px){
            padding-bottom: 15px;
        }
    }
    &-data{
        h5{
            padding-bottom: 10px;
        }
        p{
            font-size: 16px;
            line-height: 24px;
            color: var(--heading-color);
        }
    }
    &-btn{
        path{
            transition: all 0.4s;
        }
    }
}
