.uxora-countdown{
    &-section{
        padding: 230px 0 130px;
    }
    &-wrap {
        display: flex;
        gap: 70px;
        justify-content: center;
        flex-wrap: wrap;
        padding-bottom: 60px;
        @include max (991px){
            padding-bottom: 40px;
        }
    }
    &-item{
        font-size: 65px;
        font-weight: 700;
        line-height: 70px;
        letter-spacing: -1px;
        position: relative;
        text-align: center;
        p{
            font-size: 20px;
            font-weight: 500;
            line-height: 26px;
            text-align: center;
        }
    }
    &-dot {
        margin-top: 24px;
        @include max (767px){
            display: none;
        }
    }
    &-content{
        max-width: 1080px;
        margin: 0 auto;
        text-align: center;
        h1{
            padding-bottom: 20px;
        }
        p{
            max-width: 850px;
            text-align: center;
            margin: 0 auto;
        }
    }
}
