.uxora-p{
   &-wrap{
    transition: all 0.4s;
    position: relative;
    &:hover{
        .uxora-p-content{
            opacity: 1;
        }
        .uxora-p-thumb::before{
            opacity: .5;
        }
        .uxora-p-btn{
            opacity: 1;
        }
    }
   }
    &-thumb{
        position: relative;
        display: block;
        margin: 0 15px 24px;
        text-align: center;
        overflow: hidden;
        border-radius: 10px;
        white-space: nowrap;
        img{
            border-radius: 10px;
            transition: all 0.4s;
            width: 100%;
            height: 100%;
            object-fit: cover;
            &:hover{
                transform: scale(1.1);
            }
        }
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            mix-blend-mode: multiply;
            left: 0;
            z-index: 1;
            opacity: 0;
            transition: .4s;
            background: linear-gradient(135deg, #FCD569 0%, #0A23F4 100%);
        }
    }
    &-content {
        bottom: 30px;
        left: 30px;
        opacity: 0;
        z-index: 1;
        transition: all .4s;
        text-align: left;
        position: absolute;
        max-width: 405px;
        h3{
            padding-bottom: 15px;
            color: var(--white-color);
        }
        p{
            color: var(--white-color);
            white-space: wrap;
        }
        @include max (991px){
            bottom: 15px;
            left: 15px;
        }
    }
    &-btn{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: all 0.4s;
        z-index: 2;
        @include max (1600px){
            top: 40%;
        }
        @include max (1399px){
            top: 50%;
        }
        @include max (1199px){
            top: 35%;
        }
        @include max (767px){
            top: 50%;
        }
        @include max (500px){
            top: 17%;
        }
    }
}
.uxora-p-slider-init2.slick-initialized.slick-slider {
    direction: rtl;
}
.uxora-portfolio-menu{
    ul{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        flex-wrap: wrap;
        li{
            border: 1px solid var(--heading-color);
            color: var(--heading-color);
            border-radius: 3px;
            padding: 16px 26px;
            cursor: pointer;
            font-weight: 500;
            @include max(767px){
                margin-bottom: 24px;
            }
            &:last-child{
                margin-bottom: 0;
                margin-right: 0;
            }
            &.active{
                background: var(--body-color);
                color: var(--heading-color);
                border: 1px solid var(--body-color);
            }
        }
    }
}

