.uxora-pricing{
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background-color: var(--white-bg);
        border: 1px solid #142D6F1A;
        &.active{
            border: 1px solid var(--white-bg);
            box-shadow: -4px 10px 20px 0px #142D6F1A;

        }
        &:hover{
            box-shadow: -4px 10px 20px 0px #142D6F1A;
            border: 1px solid var(--white-bg);
        }
    }
    &-header{
        padding-bottom: 24px;
        &.header2{
            font-size: 28px;
            font-weight: 700;
            line-height: 36px;
            letter-spacing: -0.5px;
            text-align: left;
        }
    }
    &-price{
        gap: 5px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 40px;
        border-bottom: 2px solid #142D6F1A;
        margin-bottom: 40px;
        @include max (767px){
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
        p{
            font-weight: 500;
        }
        .uxora-price-value {
            font-weight: 700;
            color: var(--heading-color);
            font-size: 70px;
            line-height: 80px;
        }
        &.price2{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
            span{
                font-weight: 500;
            }
            .pricing-title{
                font-size: 65px;
                font-weight: 700;
                line-height: 70px;
                letter-spacing: -2px;
                text-align: left;
            }
        }
    }
    &-body{
        p{
            font-weight: 500;
        }
        ul{
            li{
                position: relative;
                font-size: 18px;
                line-height: 25px;
                padding-bottom: 15px;
                padding-left: 30px;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 3px;
                }
            }
        }
    }
    &-text{
        padding: 30px 0;
        border-bottom: 1px solid #001A3D1A;
        margin-bottom: 30px;
    }
    &-footer{
        &.extra-mt{
            margin-top: 130px;
            @include max (991px){
                margin-top: 50px;
            }
            @include max (767px){
                margin-top: 30px;
            }
        }
    }
    // end 
}

.toggle-switch {
    cursor: pointer;
    width: 260px;
    display: flex;
    align-items: center;
    border: 1px solid var(--heading-color);
    border-radius: 3px;
    margin: 0 auto;
    justify-content: center;
}
.toggle-switch input {
    display: none;
}
.toggle-switch,
.toggle-switch span {
    font-weight: 500;
}
.toggle-switch input:checked + span + span,
.toggle-switch input + span {
    padding: 13px 30px;
    background-color: var(--body-color);
}
.toggle-switch input + span + span,
.toggle-switch input:checked + span {
    background-color: transparent;
    border-color: transparent;
    padding: 16px 28px;
    color: var(--heading-color);
  }
// end switcher btn

// add table



table { 
	width: 100%;
    border: 1px solid #142D6F1A;
    border-radius: 10px!important;
}

td, th { 
    text-align: center;
	padding: 20px 25px;; 
	border-right: 1px solid rgba(20, 45, 111, .1019607843);
    @include max (1280px){
        padding: 20px;
    }
    span{
        font-size: 18px;
        line-height: 26px;
    }
}

.pricing-author{
    text-align: left;
}
.uxora-pricing-table {
    
    @include max(1349px) {
        overflow-x: auto;
    }
}
.table-sub-title{
    background-color: var(--light-color);

    th{
        border-right: 1px solid var(--light-color);
        padding: 30px;
        text-align: left;
        @include max (1260px){
            padding: 20px;
        }
        span{
            font-size: 28px;
            font-weight: 700;
            line-height: 36px;
            letter-spacing: -0.5px;
        }
    }
  
}
    
