.uxora-section-padding4{
    padding: 0 0 35px;
    overflow: hidden;
    @include max (991px){
        padding: 0 0 20px;
    }
    &.section{
        padding-top: 130px;
        @include max (991px){
           padding-top: 80px;
        }
        @include max (767px){
            padding-top: 60px;
        }
        position: relative;
        &::before{
            width: 150px;
            height: 150px;
            top: 24%;
            left: 80px;
            content: "";
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            position: absolute;
            border-radius: 100px;
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
}
.uxora-section-padding5{
    overflow: hidden;
    padding: 35px 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}

.uxora-section-padding7{
    overflow: hidden;
    padding: 130px 0 0;
    @include max (991px){
        padding: 80px 0 0;
    }
    @include max (767px){
        padding: 60px 0 0;
    }
    &.section{
        position: relative;
        &::before{
            width: 150px;
            height: 150px;
            top: 24%;
            left: 80px;
            content: "";
            position: absolute;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            border-radius: 100px;
            transform: rotate(145deg);
            background: linear-gradient(47.56deg, #a3bdff -40.67%, #f6f4ef 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
    &.section2{
        overflow: hidden;
        position: relative;
        &::before{
            width: 130px;
            height: 130px;
            top: 50%;
            right: 80px;
            content: "";
            position: absolute;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            border-radius: 100px;
            background: linear-gradient(47.56deg, #a3bdff -40.67%, #f6f4ef 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
}
.uxora-section-padding9{
    overflow: hidden;
    padding: 130px 0 35px;
    @include max (991px){
        padding: 80px 0 35px;
    }
    @include max (767px){
        padding: 60px 0 35px;
    }
}


// section title

.uxora-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-680{
        max-width: 680px;
    }
    &.max-750{
        max-width: 750px;
    }
    &.title2{
        @include max (991px){
            text-align: left;
        }
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
    }
    
}
// default content
.uxora-default-content{
    h2{
        padding-bottom: 20px;
    }
    h3{
        padding-bottom: 20px;
    }
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.pr-50{
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}
.pl-50{
    padding-left: 50px;
    @include max (991px){
        padding-left: 50px;
    }
}