.uxora-service{
  &-table-item{
    display: flex;
    align-items: center;
    padding-bottom: 50px;
    border-bottom: 1px solid #181B0E1A;
    margin-bottom: 50px;
    @include max (991px){
        padding-bottom: 30px;
        border-bottom: 1px solid #181B0E1A;
        margin-bottom: 30px;
    }
    &:last-child{
        padding-bottom: 0;
        border-bottom: none;
        margin-bottom: 0;
    }
    @include max (991px){
        display: block;
    }
  }
  &-table-title{
    flex: 0 0 35%;
    h4{
        padding-bottom: 12px;
    }
  }
  &-table-body{
    flex: 0 0 45%;
    padding: 0 0 0 80px;
    @include max (1399px){
        padding: 0;
    }
    @include max (991px){
        padding: 15px 0;
    }
  }
  &-table-btn{
    flex: 0 0 20%;
    text-align: right;
    @include max (991px){
        text-align: left;
    }
  }
}
// add process

.uxora-process{
    &-wrap{
        position: relative;
        &.pt-30{
            @include max (991px){
                padding-top: 30px;
            }
        }
        &.pt-31{
            @include max (767px){
                padding-top: 30px;
            }
        }
    }
    &-number{
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        background-color: var(--heading-color);
        h3{
            color: var(--light-color);
        }
    }
    &-content{
        h3{
            padding: 30px 0 15px 0;
        }
    }
}
