
// counter

.uxora-counter{

  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
    @include max (991px){
      margin-bottom: 40px;
    }
    @include max (767px){
      margin-bottom: 30px;
    }
    position: relative;
    &::before{
      position: absolute;
      content: "";
      width: 60px;
      height: 1px;
      top: 42px;
      left: 130px;
      background-color: var(--heading-color);
      @include max (1399px){
        display: none;
      }
      @include max (1199px){
        display: block;
        width: 110px;
        top: 30px;
        left: 110px;
      }
      @include max (991px){
        top: 24px;
      }
      @include max (767px){
        display: none;
      }
    }
    &:last-child{
      margin-bottom: 0;
    }
    @include max (30px){
      margin-bottom: 30px;
    }
    p{
      font-size: 21px;
      font-weight: 700;
      line-height: 30px;
      text-align: right;
      padding-left: 100px;
      @include max (1399px){
        padding-left: 40px;
      }
      @include max (1199px){
        padding-left: 180px;
      }
      @include max (472px){
        padding-left: 85px;
      }
    }
    &.item2{
      &::before{
        position: absolute;
        content: "";
        width: 60px;
        height: 1px;
        top: 42px;
        left: 180px;
        background-color: var(--heading-color);
        @include max (1199px){
          display: block;
          width: 110px;
          top: 30px;
          left: 150px;
        }
        @include max (991px){
          top: 24px;
        }
        @include max (767px){
          display: none;
        }
      }
    }
  }
  &-data {
    position: relative;
    display: flex;
    overflow: hidden;
    align-items: center;
    height: 80px;
    @include max (1199px){
      height: 60px;
    }
    @include max (991px){
      height: 50px;
    }
    @include max (767px){
      justify-content: center;
    }
  }
  &-thumb{
    text-align: center;
    @include max (991px){
      padding: 40px 0;
      text-align: left;
    }
    img{
      border-radius: 20px;
      @include max (991px){
        width: 100%;
      }
    }
  }
}


.uxora-counter-data > span {
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: transform 2s ease;
  transform: translateY(0);
  line-height: 1;
  
}
.data2 > span{
  width: 15%;
  @include max (1199px){
    width: 10%;
  }
}
.uxora-counter-data > span span {
  flex: 0 0 100%;
  height: 100%;
}
.uxora-counter-data:nth-child(1) > span {
  transition-delay: 0s;
}
.uxora-counter-data:nth-child(2) > span {
  transition-delay: 0.375s;
}
.uxora-counter-data:nth-child(3) > span {
  transition-delay: 0.75s;
}
.uxora-counter-data:nth-child(4) > span {
  transition-delay: 1.125s;
}
.uxora-counter-data:nth-child(5) > span {
  transition-delay: 1.5s;
}
.uxora-counter-data:nth-child(6) > span {
  transition-delay: 1.875s;
}
.uxora-counter-data:nth-child(7) > span {
  transition-delay: 2.25s;
}
.uxora-counter-data:nth-child(8) > span {
  transition-delay: 2.625s;
}
.uxora-counter-data:nth-child(9) > span {
  transition-delay: 3s;
}
.uxora-counter-data:nth-child(10) > span {
  transition-delay: 3.375s;
}
.uxora-counter-data:nth-child(11) > span {
  transition-delay: 3.75s;
}
.uxora-counter-data:nth-child(12) > span {
  transition-delay: 4.125s;
}
.uxora-counter-data:nth-child(13) > span {
  transition-delay: 4.5s;
}
.uxora-counter-data:nth-child(14) > span {
  transition-delay: 4.875s;
}
.uxora-counter-data:nth-child(15) > span {
  transition-delay: 5.25s;
}
.uxora-counter-data:nth-child(16) > span {
  transition-delay: 5.625s;
}
.uxora-counter-data:nth-child(17) > span {
  transition-delay: 6s;
}
.uxora-counter-data:nth-child(18) > span {
  transition-delay: 6.375s;
}
.uxora-counter-data:nth-child(19) > span {
  transition-delay: 6.75s;
}
.uxora-counter-data:nth-child(20) > span {
  transition-delay: 7.125s;
}


.counter-card{
  h2{
    color: var(--white-color);
    font-size: 40px;
    line-height: 48px;
    height: 40px;
  }
  span.text{
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    padding-right: 12px;
    color: var(--white-color);
  }
}