.uxora-faq{
  
  &-item{
    margin-bottom: 24px;
    background-color: var(--light-color2);
    padding: 30px;
    border-radius: 20px;
    position: sticky;
    transition: all 0.4;
    &.open {
      .uxora-faq-body{
          display: block;
        }
        .uxora-active-icon{
          transform: rotate(180deg);
        }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 10px;
    cursor: pointer;
  }
  &-body{
    display: none;
  }
}


.uxora-active-icon{
  flex-shrink: 0;
  transition: all 0.4s;
}
.faq-title{
  position: sticky;
  top: 110px;
}